body {
  background-color: #121212;
  color: #ffffff;
  margin: 0;
  font-family: Arial, sans-serif;
}

.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

.start-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.player-slider {
  margin-top: 20px;
}

h1 {
  font-size: 4em;
}

.board {
  display: inline-block;
  margin: 20px;
}

.board-row {
  display: flex;
  flex-wrap: wrap;
}

.board-cell {
  width: 100px;
  height: 40px;
  margin: 5px;
  background-color: #333;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  font-size: 14px;
  color: #ffffff;
}

button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 8px;
  background-color: #1e88e5;
  border: none;
  cursor: pointer;
  color: #ffffff;
}

button:hover {
  background-color: #1565c0;
  color: white;
}

@media (max-width: 600px) {
  h1 {
    font-size: 2.5em;
  }

  .board-cell {
    width: 80px;
    height: 30px;
    font-size: 12px;
  }

  button {
    padding: 8px 16px;
    font-size: 14px;
  }
  
  .player-slider input[type=range] {
    width: 80%;
  }
}

@media (max-width: 400px) {
  h1 {
    font-size: 2em;
  }

  .board-cell {
    width: 70px;
    height: 25px;
    font-size: 10px;
  }

  button {
    padding: 6px 12px;
    font-size: 12px;
  }
}
